.work {
    background-color: $color-yellow;

    &__inner {
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        width: 58rem;
        height: 6rem;
        font-size: 5.5rem;
        font-weight: bold;
        letter-spacing: -3.3px;
        text-align: center;
        color: $color-black;
    }

    &__subtitle {
        width: 97.8rem;
        height: 5.7rem;
        font-size: 1.7rem;
        text-align: center;
        color: $color-black;
    }

    &__img {
        width: 140rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 auto;
    }

    &__bottom {
        width: 196px;
        height: 42px;
        border-radius: 5px;
        background-color: #000000;
        margin-right: 27rem;
        transform: skewX(30deg);
        margin: 0 auto;

        button {
            color: $color-white;
            background-color: $color-black;
            border: none;
            margin: 0.7rem 4.5rem;
            padding: 0;
            font-family: Montserrat;
            font-size: 18.5px;
            font-weight: 900;
            letter-spacing: -1.11px;
            text-align: center;
            transform: skewX(-30deg);
        }
    }

}